import React from 'react'
import { Row, Col, Container } from 'react-bootstrap';
interface Book {
    desc: any,
    url: any,
    img: any,
    cat: any,
    autors: any,
}
export const Thisbook: React.FC<Book> = props => {
    return (
        <Container>
            <Row className="justify-content-md-center">
                <Col lg="6" md="12">
                    <a href={props.url}>
                        <img src={props.img} width="400" />
                    </a>

                </Col>
                <Col lg="6" md="12" className="text-center" >
                    <h3 className="text-left">
                        Autors:
                        {props.autors}
                    </h3>
                    <br />

                    <h3 className="text-left">
                    category:
                        {props.cat}
                    </h3>
                    <br />
                    <h2 className="text-left">
                    description
                    </h2>
                    <br />
                    <h3 className="text-left">
                        {props.desc}
                    </h3>

                </Col>
            </Row>

        </Container>
    )

}
