import React, { useState } from 'react'

import { Myindex } from './page/Myindex'
import { Thisbook } from './page/Thisbook'
import axios,{ Axios} from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import {
  Routes,
  Route
} from "react-router-dom";
const App: React.FunctionComponent = () => {
  const [descw, Setdesc] = useState<string>()
  const [imgw, Setimg] = useState<string>()
  const [urlw, Seturl] = useState<string>()
  const [catw, Setcat] = useState<string[]>()
  const [autorsw, Setautors] = useState<string[]>()
  const GetThisBook = (desc: string,img: string,url: string,cat: string[],autors: string[]) => {
    Setimg(img);
    Setdesc(desc);
    Seturl(url);
    Setcat(cat);
    Setautors(autors);
  };

  return (
    <div className="App">
    <Routes>
          <Route   path="/thisbook" element={<Thisbook desc={descw} url={urlw} img={imgw} cat={catw } autors={autorsw}/>}/>
          <Route  index  element={<Myindex GetThisBook={GetThisBook} />} />
        
      </Routes>,
    </div>
  );
}

export default App;
