import React, { useState, useRef } from 'react'

import { Form, Button, Row, Col, Container } from 'react-bootstrap';
import { MyNavbar } from '../components/MyNavbar'
import { Myfooter } from '../components/Myfooter'
import '../components/content.css';
import '../components/MyBanner.css'
import axios from 'axios';
import { Link } from "react-router-dom";
interface Book {
    GetThisBook(desc: string,img: string,url: string, cat: string[], autors: string[]): void
}
export const Myindex: React.FC<Book> = props => {

    const [result, Setresut] = useState<any[]>([])
    const [startIndex, SetstartIndex] = useState<number>(0);
    const refText = useRef<HTMLInputElement>(null);
    const refCategories = useRef<HTMLSelectElement>(null);
    const refSorting = useRef<HTMLSelectElement>(null);

    const NexthBook = (event: React.MouseEvent<HTMLElement>) => {
        SetstartIndex(startIndex + 30)
        axios.get("https://www.googleapis.com/books/v1/volumes?q=" + refText.current!.value + "&orderBy=" + refSorting.current!.value + "&maxResults=30&startIndex=" + startIndex).then(data => [
            Setresut([...result, ...data.data.items])
        ])
    }
    const SearchBook = (event: React.MouseEvent<HTMLElement>) => {
        axios.get("https://www.googleapis.com/books/v1/volumes?q=" + refText.current!.value + "&orderBy=" + refSorting.current!.value + "&maxResults=30&startIndex=" + startIndex).then(data => [
            Setresut(data.data.items),
            console.log(data.data.items)
            
        ])
    }
    const SearchBookKey = (event: React.KeyboardEvent) => {
        if (event.key === "Enter") {
            axios.get("https://www.googleapis.com/books/v1/volumes?q=" + refText.current!.value + "&orderBy=" + refSorting.current!.value + "&maxResults=30&startIndex=" + startIndex).then(data => [
                Setresut(data.data.items)
            ])
        }
    }


    return (
        <div >
            <MyNavbar></MyNavbar>
            <header className="header">
                <div className="header-content text-center">
                    <Container>
                        <Row className="justify-content-center">
                            <div className="col-lg-12 col-xl-9">
                                <div className="text-container">
                                    <Form>
                                        <Container className="text-center text-white" >
                                            <Row className="justify-content-md-center">
                                                <Col>
                                                    <Form.Group className="mb-3 w-100" controlId="formSearch">
                                                        <Form.Label>Search</Form.Label>
                                                        <Form.Control ref={refText} type="text" placeholder="Search" />
                                                        <Form.Text className="text-muted">
                                                            Search
                                                        </Form.Text>
                                                    </Form.Group>
                                                </Col>

                                            </Row>
                                            <Row>
                                                <Col>
                                                    <Form.Group className="mb-3" controlId="formCategories">
                                                        <Form.Label>Categories</Form.Label>
                                                        <Form.Select ref={refCategories}>
                                                            <option value="all">All</option>
                                                            <option value="art">art</option>
                                                            <option value="biography">biography</option>
                                                            <option value="computes">computes</option>
                                                            <option value="history">history</option>
                                                            <option value="medical">medical</option>
                                                            <option value="poetry">poetry</option>
                                                        </Form.Select>
                                                    </Form.Group>
                                                </Col>
                                                <Col>
                                                    <Form.Group className="mb-3" controlId="formBasicPassword">
                                                        <Form.Label>Sorting by</Form.Label>
                                                        <Form.Select ref={refSorting}>
                                                            <option value="relevance">relevance</option>
                                                            <option value="newest">newest</option>

                                                        </Form.Select>
                                                    </Form.Group>
                                                </Col>

                                            </Row>
                                        </Container>
                                        <Button onKeyPress={SearchBookKey} onClick={SearchBook} variant="primary">
                                            Submit
                                        </Button>
                                    </Form>

                                </div>

                            </div>

                        </Row>

                    </Container>

                </div>

            </header >

            <svg className="header-frame" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="none" viewBox="0 0 1920 310"><defs></defs><title>header-frame</title><path className="cls-1" d="M0,283.054c22.75,12.98,53.1,15.2,70.635,14.808,92.115-2.077,238.3-79.9,354.895-79.938,59.97-.019,106.17,18.059,141.58,34,47.778,21.511,47.778,21.511,90,38.938,28.418,11.731,85.344,26.169,152.992,17.971,68.127-8.255,115.933-34.963,166.492-67.393,37.467-24.032,148.6-112.008,171.753-127.963,27.951-19.26,87.771-81.155,180.71-89.341,72.016-6.343,105.479,12.388,157.434,35.467,69.73,30.976,168.93,92.28,256.514,89.405,100.992-3.315,140.276-41.7,177-64.9V0.24H0V283.054Z" /></svg>
            <Container className="shop-home-list section">

                <Row>
                    <Col>
                        <div className="shop-section-title">
                            <h1>Books</h1>
                        </div>
                    </Col>
                </Row>
                <Row>
                    {result.map(
                        book => (
                            <Col className="single-list" md="4" lg="3">
                                <Link to="/thisbook" onClick={() => {
                                    if(book.volumeInfo.authors.length==0)
                                    {
                                        book.volumeInfo.authors="";
                                    }
                                    if(book.volumeInfo.categories.length==0)
                                    {
                                        book.volumeInfo.categories="";
                                    }
                              
                                    props.GetThisBook(
                                        book.volumeInfo.description, 
                                        book.volumeInfo.imageLinks.thumbnail, 
                                        book.volumeInfo.previewLink,
                                        book.volumeInfo.authors,
                                        book.volumeInfo.categories
                                    ) 
                                }}>
                                    <div className="list-image overlay">
                                        {
                                            book.volumeInfo.readingModes.image==true?<img src={book.volumeInfo.imageLinks.thumbnail} alt={book.volumeInfo.title} />: <img src="https://upload.wikimedia.org/wikipedia/commons/9/9a/%D0%9D%D0%B5%D1%82_%D1%84%D0%BE%D1%82%D0%BE.png" alt={book.volumeInfo.title} />
                                        }
                                      
                                    </div>

                                    <div className="content">
                                        <h4 className="title">{book.volumeInfo.title}</h4>          
                                        <p className="price with-discount">{book.volumeInfo.categories}</p>
                                    </div>
                                </Link>
                            </Col>
                        )
                    )}
                </Row>
                <Row>
                    <Button onClick={NexthBook} variant="primary">
                        Submit
                    </Button>

                </Row>
            </Container>

            <Myfooter></Myfooter>
        </div >
    );
}
